import axios from '@/utils/uniAxios.js'
import {
	mapState
} from 'vuex';
let startY = 0,
	moveY = 0,
	pageAtTop = true;
export default {
	components: {
		// uniCard
	},
	data() {
		return {
			shoucang: 0,
			zuji: 0,
			coverTransform: 'translateY(0px)',
			coverTransition: '0s',
			moving: false,
			abImg: '../../static/banner_01.png',
			goodsList: [{
					img: '../../static/egg_01.png',
					title: '正宗农家散养谷饲土鸡蛋20枚',
					subTitle: '宇宙无敌巨好吃的鸡蛋',
					jifen: '200',
					price: 999.90
				},
				{
					img: '../../static/egg_01.png',
					title: '正宗农家散养谷饲土鸡蛋20枚',
					subTitle: '宇宙无敌巨好吃的鸡蛋',
					jifen: '200',
					price: 999.90
				},
				{
					img: '../../static/egg_01.png',
					title: '正宗农家散养谷饲土鸡蛋20枚',
					subTitle: '宇宙无敌巨好吃的鸡蛋',
					jifen: '200',
					price: 999.90
				}
			],
			authShow: true,
			isCanUse: uni.getStorageSync('isCanUse') || true //默认为true
		}
	},
	onLoad(option) {
		this.loadData();
	},
	computed: {
		...mapState(['hasLogin', 'userInfo'])
	},
	methods: {
		// 授权用户信息
		wxGetUserInfo() {
			let _this = this;
			uni.getUserInfo({
				provider: 'weixin',
				success: function(infoRes) {
					this.authShow = false
					console.log(infoRes)
					let nickName = infoRes.userInfo.nickName; //昵称
					let avatarUrl = infoRes.userInfo.avatarUrl; //头像
					let mobile = infoRes.userInfo.mobile; //头像
					_this.userInfo.nickname = nickName;
					_this.userInfo.portrait = avatarUrl;
					_this.userInfo.mobile = mobile ? mobile : '';
					_this.userInfo.gender = infoRes.userInfo.gender;
					// _this.userInfo = infoRes.userInfo;
					_this.$store.commit('login', _this.userInfo)
					try {
						uni.setStorageSync('isCanUse', false); //记录是否第一次授权  false:表示不是第一次授权
						_this.updateUserInfo();
					} catch (e) {}
				},
				fail(res) {}
			})
		},
		login() {
			let _this = this;
			// 1.wx获取登录用户code
			uni.login({
				provider: 'weixin',
				success: function(loginRes) {
					let code = loginRes.code;
					if (!_this.isCanUse) {
						//非第一次授权获取用户信息
						uni.getUserInfo({
							provider: 'weixin',
							success: function(infoRes) {
								//获取用户信息后向调用信息更新方法
								let nickName = infoRes.userInfo.nickName; //昵称
								let avatarUrl = infoRes.userInfo.avatarUrl; //头像
								_this.updateUserInfo(); //调用更新信息方法
							}
						});
					}
					//2.将用户登录code传递到后台置换用户SessionKey、OpenId等信息
					// uni.request({
					// 	url: '服务器地址',
					// 	data: {
					// 		code: code,
					// 	},
					// 	method: 'GET',
					// 	header: {
					// 		'content-type': 'application/json'
					// 	},
					// 	success: (res) => {
					// 		//openId、或SessionKdy存储//隐藏loading
					// 		uni.hideLoading();
					// 	}
					// })
				}
			})
		},
		// 账号管理-个人资料
		accountManage() {
			// 手机号登录，或者微信登录，不是第一次登录
			if (uni.getStorageSync('setPhone') || uni.getStorageSync('gt') || !uni.getStorageSync('isCanUse')) {
				uni.navigateTo({
					url: '../set/set?id=' + this.userInfo.id
				})
			} else {
				uni.navigateTo({
					url: '../public/login'
				})
			}
		},
		// 从登录页跳转过来，刷新页面,获取用户信息
		loadData() {
			// 优先使用微信登录
			let hasLogin = this.$store.state.hasLogin;
			if(hasLogin){
				this.authShow = false;
				// let setUserInfo = this.$store.state.userInfo;
				return;
			}else if(uni.getStorageSync('setPhone')){//使用手机号登陆
				axios.post('/sso/user/userInfo').then(res => {
					if (res.data.code = '200') {
						this.authShow = false;
						this.userInfo.mobile = res.data.data.phone;
						this.userInfo.id = res.data.data.id;
				
						this.$store.commit('login', this.userInfo)
					}
				})
			}
		},

		// 查看更多订单
		lookOrder() {
			uni.navigateTo({
				url: '../order/order?state=0'
			});
		},
		/**
		 * 统一跳转接口,拦截未登录路由
		 * navigator标签现在默认没有转场动画，所以用view
		 */
		navTo(url) {
			console.log(this.hasLogin)
			if (!this.hasLogin) {
				url = '/pages/public/login';
			}
			uni.navigateTo({
				url
			})
		},

		/**
		 *  会员卡下拉和回弹
		 *  1.关闭bounce避免ios端下拉冲突
		 *  2.由于touchmove事件的缺陷（以前做小程序就遇到，比如20跳到40，h5反而好很多），下拉的时候会有掉帧的感觉
		 *    transition设置0.1秒延迟，让css来过渡这段空窗期
		 *  3.回弹效果可修改曲线值来调整效果，推荐一个好用的bezier生成工具 http://cubic-bezier.com/
		 */
		coverTouchstart(e) {
			if (pageAtTop === false) {
				return;
			}
			this.coverTransition = 'transform .1s linear';
			startY = e.touches[0].clientY;
		},
		coverTouchmove(e) {
			moveY = e.touches[0].clientY;
			let moveDistance = moveY - startY;
			if (moveDistance < 0) {
				this.moving = false;
				return;
			}
			this.moving = true;
			if (moveDistance >= 80 && moveDistance < 100) {
				moveDistance = 80;
			}

			if (moveDistance > 0 && moveDistance <= 80) {
				this.coverTransform = `translateY(${moveDistance}px)`;
			}
		},
		coverTouchend() {
			if (this.moving === false) {
				return;
			}
			this.moving = false;
			this.coverTransition = 'transform 0.3s cubic-bezier(.21,1.93,.53,.64)';
			this.coverTransform = 'translateY(0px)';
		}
	}
}
